.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.icon--default {
  color: #333;
}
.icon--white {
  color: white;
}
.icon--primary {
  color: #2550aa;
}
.icon--color-2 {
  color: #4c4c4c;
}
.icon--color-3 {
  color: #7b7b7b;
}
.icon--warning {
  color: #ffb000;
}
.icon--success {
  color: #29b662;
}
.icon--alert {
  color: #ff3245;
}
.icon--info {
  color: #5d92ff;
}
.icon--spin {
  display: inline-block;
  animation: spinIcon 1s linear infinite;
}

@keyframes spinIcon {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}