.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
.hlarge {
  color: #1f1f1f;
  font-weight: 400;
  display: block;
}

.hlarge {
  font-size: 36px;
  margin-bottom: 25px;
  line-height: 42px;
}

.h1 {
  font-size: 28px;
  margin-bottom: 25px;
  line-height: 42px;
}

.h2 {
  font-size: 22px;
  margin-bottom: 22px;
  line-height: 36px;
}

.h3 {
  font-size: 18px;
  margin-bottom: 18px;
  line-height: 26px;
}

.h4 {
  font-size: 16px;
  margin-bottom: 8px;
  line-height: 22px;
}

.h5 {
  font-size: 14px;
  margin-bottom: 4px;
  line-height: 20px;
}

.h6 {
  font-size: 12px;
  margin-bottom: 2px;
  line-height: 18px;
}

.heading--left {
  text-align: left;
}
.heading--center {
  text-align: center;
}
.heading--right {
  text-align: right;
}
.heading--no-margin {
  margin-bottom: 0;
}
.heading--bold {
  font-weight: 700;
}
.heading__color--white {
  color: white;
}
.heading__color--brand {
  color: #2550aa;
}