.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.radio {
  display: block;
  position: relative;
  padding-left: 30px;
  cursor: pointer;
  font-size: 14px;
  margin: 0;
  line-height: 24px;
  opacity: 1;
  height: 0;
}
.radio label {
  font-weight: 400;
  margin: 0;
  font-size: 14px;
  color: #4c4c4c;
  cursor: pointer;
  top: unset !important;
  left: unset !important;
  opacity: 1 !important;
  pointer-events: none;
}
.radio:checked ~ .radio__overlay:after {
  display: block;
}
.radio input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.radio input:checked ~ .radio__overlay {
  background: #2550aa;
  border-color: #2550aa;
}
.radio input:checked ~ .radio__overlay:after {
  display: block;
}
.radio input:disabled {
  color: #cccccc;
}
.radio input:disabled ~ .radio__overlay {
  background: white;
  border: 2px solid #cccccc;
  pointer-events: none;
}
.radio input:disabled ~ .radio__overlay:after {
  border: 0;
}
.radio input:disabled:checked ~ .radio__overlay:after {
  border: 2px solid white;
  background: #cccccc;
}
.radio .radio__overlay {
  box-sizing: border-box;
  position: absolute;
  top: 2px;
  left: 0;
  height: 18px;
  width: 18px;
  background: white;
  border: 2px solid #a2a2a2;
  border-radius: 50%;
}
.radio .radio__overlay:after {
  box-sizing: unset;
  content: "";
  position: absolute;
  display: none;
  left: 0;
  top: 0;
  width: 10px;
  height: 10px;
  border: 2px solid white;
  border-radius: 50%;
  background: #2550aa;
}