.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.color-number {
  margin: 0;
  font-weight: 600;
}
.color-number__color--success {
  color: #29b662;
}
.color-number__color--warning {
  color: #ffb000;
}
.color-number__color--alert {
  color: #ff3245;
}
.color-number__color--progress {
  color: #009fe3;
}
.color-number__color--default {
  color: black;
}
.color-number__color--brand {
  color: #2550aa;
}
.color-number__color--white {
  color: white;
}
.color-number__size--10 {
  font-size: 10px;
}
.color-number__size--12 {
  font-size: 12px;
}
.color-number__size--16 {
  font-size: 16px;
}
.color-number__size--18 {
  font-size: 18px;
}
.color-number__size--32 {
  font-size: 32px;
}
.color-number__size--44 {
  font-size: 44px;
}
.color-number__size--62 {
  font-size: 62px;
}