.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.tab {
  cursor: pointer;
  line-height: 1;
  color: #7b7b7b;
  display: block;
}
.tab--active {
  position: relative;
  color: #2550aa;
  font-weight: 600;
}
.tab--active:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -2px;
  width: 100%;
  height: 4px;
  background-color: #2550aa;
}
.tab--active:hover button, .tab--active:active button, .tab--active:focus button {
  color: #2550aa;
}
.tab__list {
  display: flex;
  justify-content: flex-start;
  border-bottom: 2px solid #dddddd;
}
.tab__panel {
  background-color: white;
}