.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.dialog {
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: 250999;
}

.modal {
  position: relative;
  left: -50%;
  transform: translateY(-50%);
  background: white;
  box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.1);
  width: 780px;
  max-width: 100%;
  max-height: 100%;
  z-index: 998;
}
.modal__header {
  display: flex;
  border-radius: 6px;
  justify-content: space-between;
  align-items: center;
  padding: 30px 40px 0;
}
.modal__header--close-only {
  justify-content: flex-end;
}
.modal__header span {
  flex: 1;
  margin: 0;
  line-height: 1;
}
.modal__component {
  width: 100%;
  display: flex;
  border-radius: 6px;
  justify-content: space-between;
  align-items: flex-start;
  padding: 25px 40px 0;
}
.modal__component--child {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal__body {
  padding: 30px 40px;
}
.modal__body--has-footer {
  padding: 30px 40px 0;
}
.modal__body--no-padding {
  padding: 0;
}
.modal__footer-divider {
  padding: 0px 40px;
}
.modal__footer {
  display: flex;
  align-items: center;
  padding: 0 40px 30px;
}
.modal__footer button {
  margin-left: 15px;
}
.modal__footer--align-right {
  justify-content: flex-end;
}
.modal__footer--align-center {
  justify-content: center;
}
.modal__footer--align-left {
  justify-content: flex-start;
}
.modal--fullscreen {
  width: 100vw;
  height: 100vh;
  z-index: 250999;
  background: white;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: stretch;
  border-radius: 0;
}
.modal--small {
  width: 764px;
}
.modal--medium {
  width: 880px;
}
.modal--big {
  width: 1080px;
}
.modal--huge-responsive {
  width: 85vw;
  max-width: 1280px;
}
.modal--topbar {
  width: 100vw;
  height: 100vh;
  z-index: 250999;
  border-radius: 0;
  background-color: #ececec;
}
.modal--topbar :global .Modal__modal__header {
  height: 60px;
  background: white;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  padding: 0 28px;
}
.modal--page {
  width: 100vw;
  height: 100vh;
  z-index: 250999;
  background: #f5f5f5;
  border-radius: 0;
}
.modal--page :global .Modal__modal__body {
  padding: 0;
}
.modal--page :global .Modal__modal__body > div {
  max-height: 100vh !important;
}
.modal--center {
  align-items: center;
}