.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.box-outline {
  position: relative;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 10px;
  cursor: pointer;
  transition: all 0.3s ease-out;
}
.box-outline__disabled {
  opacity: 0.5;
  pointer-events: none;
  filter: grayscale(1);
}
.box-outline__default {
  border: 1px solid transparent;
}
.box-outline__default--hover:hover {
  border: 1px solid #2550aa;
  box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.1);
}
.box-outline__background {
  border: 1px solid #dddddd;
}
.box-outline__background--hover:hover {
  border: 1px solid #2550aa;
  background: #f5f7fc;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}
.box-outline__no-padding {
  padding: 0;
}
.box-outline.box-outline__is-selected {
  border: 1px solid #29b662;
}
.box-outline.box-outline__is-selected--hover:hover {
  border: 1px solid #29b662;
  box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.1019607843);
}
.box-outline__checkmark-status {
  box-sizing: border-box;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  font-size: 12px;
  z-index: 3;
}
.box-outline__checkmark-status--is-selected {
  background-color: #29b662;
}