.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.search {
  display: flex;
  align-content: center;
  align-items: center;
  flex: 1;
  box-sizing: border-box;
}
.search__input,
.search input {
  box-sizing: border-box;
  border: 0;
  padding: 0 36px 0 44px;
  flex: 1;
  line-height: 1;
  position: absolute;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  box-shadow: none;
  font-size: 1em;
}
.search__input::placeholder,
.search input::placeholder {
  color: #a7a7a7;
}
.search__input:focus, .search__input:active,
.search input:focus,
.search input:active {
  box-shadow: 0px 3px 6px 0px rgba(221, 221, 221, 0.7);
  outline: none;
}
.search__clean {
  position: absolute;
  right: 2px;
}
.search__icon {
  z-index: 1;
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
}
.search__icon svg,
.search__icon i {
  color: #d8d8d8;
}
.search__icon-button {
  z-index: 1;
  position: absolute;
  left: 2px;
}
.search--small {
  height: 28px;
  font-size: 12px;
  padding: 0 36px 0 34px;
}
.search--medium {
  height: 38px;
  font-size: 14px;
}
.search--large {
  height: 44px;
  font-size: 16px;
}
.search--input {
  border: 1px solid #dddddd;
  position: relative;
}