.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.step__number {
  margin-right: 15px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  font-weight: 600;
  color: white;
}
.step__number--small {
  top: 0;
  left: -9px;
  width: 18px;
  height: 18px;
}
.step__number--medium {
  top: 0;
  left: -11px;
  width: 22px;
  height: 22px;
}
.step__number--completed {
  background: #29b662;
}
.step__number--current {
  background: #2550aa;
}
.step__number--next {
  background: #cccccc;
}
.step__number--absolute {
  position: absolute;
  z-index: 30;
}