.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.container {
  display: flex;
}
.container:before {
  content: "";
  width: 24px;
  height: 12px;
  margin-right: 15px;
  margin-top: 9px;
}
.container--green:before {
  background: #29b662;
}
.container--yellow:before {
  background: #ffb000;
}
.container--pink:before {
  background: #f27589;
}
.container--description {
  display: flex;
  flex-direction: column;
}