.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.alert-message {
  position: relative;
  padding: 10px 15px;
  line-height: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #1f1f1f;
}
.alert-message .alert__message {
  padding: 0 15px;
  text-align: left;
  flex: 2;
  margin: 0;
  font-size: 12px;
  line-height: 16px;
  width: 210px;
}
.alert-message--alert {
  background: #ffebed;
  border: 1px solid #ff3245;
}
.alert-message--alert svg {
  align-items: center;
  color: #ff3245;
}
.alert-message--warning {
  background: #fff7e5;
  border: 1px solid #ffb000;
}
.alert-message--warning svg {
  align-items: center;
  color: #ffb000;
}
.alert-message--success {
  background: #e9f8ef;
  border: 1px solid #29b662;
}
.alert-message--success svg {
  align-items: center;
  color: #29b662;
}
.alert-message--info {
  background: #e5edff;
  border: 1px solid #5d92ff;
}
.alert-message--info svg {
  align-items: center;
  color: #5d92ff;
}