.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.form--has-error input,
.form--has-error select {
  border-color: #df2439 !important;
  border-width: 2px !important;
}
.form--has-error input {
  box-shadow: none;
}
.form--has-error label {
  color: #df2439;
}

.form-field {
  position: relative;
  min-height: 62px;
}
.form-field .label {
  padding: 0 0 5px;
}
.form-field .input__error {
  color: #df2439;
  font-size: 12px;
  display: block;
}
.form-field--has-error {
  margin-bottom: 15px;
}
.form-field--has-error label {
  color: #df2439;
  position: absolute;
  top: 20px;
  left: 0;
  text-transform: uppercase;
  transition: opacity 0.3s, top 0.3s;
  opacity: 0;
}
.form-field--has-error select {
  border-color: #df2439;
  border-width: 2px;
}

.input__label {
  font-size: 16px;
  height: 20px;
  font-weight: 400;
  color: #7b7b7b;
  margin: 0;
  left: 0;
  position: absolute;
  pointer-events: none;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
  opacity: 0;
}

.input__wrapper {
  display: flex;
  flex-direction: column;
  min-height: 38px;
  position: relative;
}
.input__wrapper input:focus ~ label,
.input__wrapper input:valid ~ label {
  top: -14px;
  font-size: 14px;
}
.input__wrapper input:valid ~ label {
  opacity: 1;
}
.input__wrapper input:invalid ~ label {
  top: 11px;
  font-size: 16px;
}
.input__wrapper input:invalid:focus {
  box-shadow: none;
}
.input__wrapper--show-label input ~ label,
.input__wrapper--show-label input:focus ~ label,
.input__wrapper--show-label input:valid ~ label,
.input__wrapper--show-label input:invalid ~ label {
  top: -14px;
  font-size: 14px;
  opacity: 1;
}
.input__wrapper--focused input ~ label,
.input__wrapper--focused input:focus ~ label,
.input__wrapper--focused input:valid ~ label,
.input__wrapper--focused input:invalid ~ label {
  top: -14px;
  font-size: 14px;
  opacity: 1;
}
.input__wrapper--focused--table {
  min-height: 22px;
}
.input__wrapper button {
  margin-right: 15px;
}
.input__wrapper .input {
  border: 0;
  border-bottom: 1px solid #dddddd;
  color: #1f1f1f;
  font-size: 16px;
  height: 38px;
  outline: none;
  line-height: 1;
  letter-spacing: unset;
  padding: 0;
  box-shadow: none;
  width: auto;
}
.input__wrapper .input:focus {
  outline: none;
  box-shadow: none;
}
.input__wrapper .input:focus ~ .bar:before,
.input__wrapper .input:focus ~ .bar:after {
  width: 100%;
}
.input__wrapper .input.error {
  border-color: #df2439;
  border-width: 2px;
  box-shadow: none;
}
.input__wrapper .input::placeholder {
  color: #a7a7a7;
  border-style: dotted;
  border-color: #dddddd;
}
.input__wrapper .input:disabled {
  border-color: #a7a7a7;
  border-style: dotted;
  color: #dddddd;
  background-color: transparent;
}
.input__wrapper .input--transparent {
  background-color: transparent;
}
.input__wrapper .input--grayed-placeholder {
  color: #7b7b7b;
}
.input__wrapper .input--hidden-arrows::-webkit-outer-spin-button, .input__wrapper .input--hidden-arrows::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.input__wrapper .input--hidden-arrows[type=number] {
  -moz-appearance: textfield;
}
.input__wrapper .input--table {
  width: 100%;
  font-size: 14px;
  height: 22px;
}

::-webkit-input-placeholder {
  border: 0;
}

::-moz-placeholder {
  border: 0;
}

:-ms-input-placeholder {
  border: 0;
}

:-moz-placeholder {
  border: 0;
}

.bar {
  position: relative;
  display: block;
  width: 100%;
}

.bar:before,
.bar:after {
  content: "";
  height: 2px;
  width: 0;
  bottom: 0;
  position: absolute;
  background: #1f1f1f;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

.bar:before {
  left: 0;
}

.bar:after {
  right: auto;
}

input:focus ~ .bar:before,
input:focus ~ .bar:after {
  width: 100%;
}

.input__tooltip {
  position: absolute;
  top: -14px;
  right: 0;
}
.input__tooltip--form {
  top: 8px;
}