.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.status-icon {
  display: inline-flex;
  border-radius: 50%;
}
.status-icon--active {
  padding: 0;
  background-color: #29b662;
}
.status-icon--inactive {
  padding: 0;
  background-color: #cccccc;
}
.status-icon--tiny {
  min-height: 15px;
  min-width: 15px;
  height: 15px;
  width: 15px;
}
.status-icon--tiny i {
  color: white;
  font-size: 7px;
  margin: auto;
}
.status-icon--medium {
  min-height: 20px;
  min-width: 20px;
  height: 20px;
  width: 20px;
}
.status-icon--medium i {
  color: white;
  font-size: 0.75em;
  margin: auto;
}