.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.notification {
  position: relative;
  padding: 10px 15px;
  line-height: 1;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.1);
}
.notification i {
  align-items: center;
  color: white;
}
.notification button {
  background: transparent;
  color: white;
}
.notification button:hover, .notification button:active, .notification button:focus {
  background: transparent;
  color: white;
}
.notification button:hover i, .notification button:active i, .notification button:focus i {
  color: white;
}

.notification__message {
  padding: 0 15px;
  text-align: left;
  flex: 2;
  margin: 0;
  font-size: 14px;
  width: 210px;
}

.notification--alert {
  background: #ff3245;
}
.notification--warning {
  background: #ffb000;
}
.notification--success {
  background: #29b662;
}
.notification--info {
  background: #5d92ff;
}
.notification--pending {
  background: #7988a0;
}

.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.time {
  margin-top: 3px;
  background-color: white;
  display: block;
  animation: load 5s normal forwards;
  height: 3px;
  left: 0;
}

@keyframes load {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}