.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas: ". . ." ". . ." ". . ." ". . .";
  margin: 0 7px;
  width: 300px;
}

.button_month {
  color: #4c4c4c;
  background: white;
  outline: 0;
  border: none;
  font-size: 12px;
  font-weight: 300;
  padding: 3px 0;
  position: relative;
  height: 30px;
}
.button_month--name {
  color: #4c4c4c;
  width: 100%;
  height: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.button_month--marker {
  position: absolute;
  top: 1px;
  height: 28px;
  width: 100%;
  z-index: 1;
}
.button_month--not-selecting:hover .button_month--marker {
  border: 1px solid #dee7eb;
}
.button_month--selecting .button_month--marker {
  border-top: 1px solid #dee7eb;
  border-bottom: 1px solid #dee7eb;
}
.button_month--first {
  padding-left: 4px;
}
.button_month--first .button_month--name {
  font-weight: 600;
}
.button_month--first .button_month--marker {
  border-left: 1px solid #dee7eb;
  left: 2px;
  width: calc(100% - 2px);
}
.button_month--last {
  padding-right: 4px;
}
.button_month--last .button_month--name {
  font-weight: 600;
}
.button_month--last .button_month--marker {
  border-right: 1px solid #dee7eb;
  right: 2px;
  width: calc(100% - 2px);
}
.button_month--selected .button_month--name {
  color: #1f1f1f;
  background: #edecec;
}
.button_month--selected:not(:hover) .button_month--marker {
  display: none;
}
.button_month--disabled {
  pointer-events: none;
  background: #f8f8f8;
}
.button_month--disabled .button_month--name {
  color: #aeb9bf;
}

.button_month--first.button_month--last .button_month--marker {
  width: calc(100% - 4px);
  border-top: 1px solid #dee7eb;
  border-bottom: 1px solid #dee7eb;
}

.year {
  font-size: 12px;
  font-weight: 600;
  color: #1f1f1f;
}

.year-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.month-range-picker > button {
  display: block;
  margin: 0 15px 5px auto;
  width: 100px;
}