.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.dot {
  height: 12px;
  width: 12px;
  border-radius: 50%;
  display: inline-block;
}
.dot--brand {
  background: #2550aa;
}
.dot--accent-1 {
  background: #9368b3;
}
.dot--accent-2 {
  background: #00bd9e;
}
.dot--accent-3 {
  background: #f99e31;
}
.dot--accent-4 {
  background: #24bd9c;
}
.dot--accent-5 {
  background: #610fb7;
}
.dot--accent-6 {
  background: #9363b6;
}
.dot--accent-7 {
  background: #f27589;
}
.dot--alert {
  background: #ff3245;
}
.dot--warning {
  background: #ffb000;
}
.dot--success {
  background: #29b662;
}
.dot--info {
  background: #5d92ff;
}
.dot--pending {
  background: #7988a0;
}