.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.form-field__dropdown--has-error {
  border-color: #df2439;
  border-width: 2px;
}
.form-field__label {
  position: relative;
  top: 35px;
  left: 0px;
  font-size: 16px;
}
.form-field__label--has-error {
  color: #df2439;
}
.form-field__label--has-label {
  top: 0;
  font-size: 14px;
}

.search-container {
  margin: 0 15px 15px;
}