.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.dropdown {
  position: fixed;
  z-index: 5;
  box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.1);
  background: white;
}
.dropdown__head {
  padding: 0 15px;
}
.dropdown__head > span {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.dropdown__wrapper {
  z-index: 1;
  color: #1f1f1f;
  cursor: pointer;
  line-height: 1;
  position: relative;
  min-width: 36px;
  min-height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
  box-sizing: border-box;
}
.dropdown__wrapper__icon {
  visibility: hidden;
}
.dropdown__wrapper:hover {
  background: #edecec;
}
.dropdown__wrapper:hover :global .Dropdown-module__dropdown__wrapper__icon {
  visibility: visible;
}
.dropdown__wrapper:hover :global .Dropdown-module__dropdown__wrapper__icon i {
  color: #7b7b7b;
}
.dropdown__wrapper--isOnlyIcon {
  padding: 0;
}
.dropdown__wrapper--spaced {
  justify-content: space-between;
}
.dropdown__wrapper--end {
  justify-content: flex-end;
}
.dropdown__wrapper i {
  color: #7b7b7b;
}
.dropdown__wrapper--input {
  border: 0;
  border-bottom: 1px solid #dddddd;
  color: #1f1f1f;
  font-size: 16px;
  padding: 0;
  outline: none;
  line-height: 1;
  border-radius: 0px;
  margin: 0 10px;
  position: relative;
}
.dropdown__wrapper--input:focus, .dropdown__wrapper--input:hover, .dropdown__wrapper--input:active {
  outline: none;
  background-color: transparent;
}
.dropdown__wrapper--input:focus:after, .dropdown__wrapper--input:hover:after, .dropdown__wrapper--input:active:after {
  content: "";
  height: 2px;
  background: #1f1f1f;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
}
.dropdown__wrapper--as-input {
  height: 38px;
  margin: 0;
}
.dropdown__wrapper--as-input span {
  padding: 0;
  font-size: 16px;
}
.dropdown__wrapper--input--disabled {
  color: #dddddd;
  border-bottom: 1px dotted #a7a7a7;
  cursor: not-allowed;
}
.dropdown__wrapper--input--disabled label {
  cursor: not-allowed;
}
.dropdown__wrapper--input--disabled:hover i,
.dropdown__wrapper--input--disabled:hover svg {
  color: #7b7b7b;
}
.dropdown__wrapper--input--disabled:focus:after, .dropdown__wrapper--input--disabled:hover:after, .dropdown__wrapper--input--disabled:active:after {
  display: none;
}
.dropdown__body {
  padding: 20px 0 10px;
}
.dropdown__body label {
  padding: 0 15px 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.dropdown__label {
  padding: 0 15px 0 15px;
  font-weight: 400;
  margin: 0;
  font-size: 14px;
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: normal;
}
.dropdown__label svg,
.dropdown__label i {
  padding-right: 10px;
}
.dropdown__label--placeholder {
  color: #a7a7a7;
}
.dropdown--auto {
  width: auto;
}
.dropdown--mini {
  width: 80px;
}
.dropdown--small {
  width: 180px;
}
.dropdown--medium {
  width: 220px;
}
.dropdown--big {
  width: 240px;
}
.dropdown--large {
  width: 280px;
}
.dropdown--huge {
  width: 480px;
}
.dropdown--extra-huge {
  width: 720px;
}
.dropdown--hidden {
  overflow: hidden;
  opacity: 0;
}