.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.time-select__label {
  margin-right: 34px;
}
.time-select__label input {
  color: #1f1f1f;
  width: 50px !important;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 10px;
}
.time-select__label input::-webkit-outer-spin-button,
.time-select__label input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
  border: none;
  background-color: transparent;
  box-shadow: none;
}
.time-select__label input[type=text] {
  -moz-appearance: textfield;
  border: none;
  background-color: transparent;
  box-shadow: none;
}
.time-select__label svg[data-icon=clock] {
  padding-right: 0;
  color: #1f1f1f;
}
.time-select__label--disabled input {
  color: #dddddd;
  -webkit-text-fill-color: #dddddd;
  cursor: not-allowed;
}
.time-select__label--disabled svg[data-icon=clock] {
  color: #dddddd;
}
.time-select__dropdown {
  width: 145px;
}
.time-select__clock-type-select {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}