.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.sidebarSkeleton {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 15px;
}
.sidebarSkeleton .item {
  width: 172px;
  height: 36px;
  margin-left: 28px;
}
.sidebarSkeleton > .item:nth-child(2) .line, .sidebarSkeleton > .item:nth-child(4) .line {
  width: 60px;
}
.sidebarSkeleton .circle {
  float: left;
  width: 14px;
  height: 14px;
  background-color: #ccc;
  border-radius: 100%;
  margin-right: 10px;
  background-image: linear-gradient(90deg, #24328e 0px, #2b3ba5 40px, #24328e 80px);
  background-size: 600px;
  animation: shine-circle-sidebar 1.6s infinite linear;
}
.sidebarSkeleton .line {
  float: left;
  width: 100px;
  height: 14px;
  border-radius: 7px;
  background-image: linear-gradient(90deg, #24328e 0px, #2b3ba5 40px, #24328e 80px);
  background-size: 600px;
  animation: shine-lines-sidebar 1.6s infinite linear;
}
.sidebarSkeleton .line ~ .line {
  background-color: #ddd;
}
@media all and (max-width: 1339px) {
  .sidebarSkeleton .item {
    width: 20px;
    margin-left: 0;
  }
  .sidebarSkeleton .line {
    display: none;
  }
}

.drawerSkeleton {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 5px;
}
.drawerSkeleton .item {
  padding: 15px;
  justify-content: center;
}
.drawerSkeleton .item .circle {
  float: left;
  width: 20px;
  height: 20px;
  background-color: #ccc;
  border-radius: 100%;
  margin: 8px 10px 10px 10px;
  justify-content: center;
  background-image: linear-gradient(90deg, #dddddd 0px, #eaeaea 40px, #dddddd 80px);
  background-size: 600px;
  animation: shine-circle 1.6s infinite linear;
}
.drawerSkeleton .item .line {
  float: left;
  width: 70px;
  height: 10px;
  border-radius: 7px;
  margin-top: 4px;
  background-image: linear-gradient(90deg, #dddddd 0px, #eaeaea 40px, #dddddd 80px);
  background-size: 600px;
  animation: shine-lines 1.6s infinite linear;
}
.drawerSkeleton .item .line ~ .line {
  background-color: #ddd;
}
.drawerSkeleton .item > .line:nth-child(3) {
  margin-top: 8px;
  width: 140px;
}

.tableSkeleton {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 20px;
}
.tableSkeleton .item {
  margin: 0 30px;
  display: flex;
  justify-content: space-between;
}
.tableSkeleton .item .line {
  width: 200px;
  height: 25px;
  border-radius: 7px;
  background-image: linear-gradient(90deg, #dddddd 0px, #eaeaea 40px, #dddddd 80px);
  background-size: 600px;
  animation: shine-lines 1.6s infinite linear;
  margin: 10px 20px;
}
.tableSkeleton .item .line ~ .line {
  background-color: #ddd;
}

.skeleton .circle {
  float: left;
  width: 20px;
  height: 20px;
  background-color: #ccc;
  border-radius: 100%;
  margin-right: 20px;
  background-image: linear-gradient(90deg, #dddddd 0px, #f5f5f5 40px, #dddddd 80px);
  background-size: 600px;
  animation: shine-circle 1.6s infinite linear;
}
.skeleton .line {
  float: left;
  width: 100px;
  height: 20px;
  border-radius: 10px;
  margin-right: 20px;
  background-image: linear-gradient(90deg, #dddddd 0px, #f5f5f5 40px, #dddddd 80px);
  background-size: 600px;
  animation: shine-lines 1.6s infinite linear;
}

@keyframes shine-lines {
  0% {
    background-position: -100px;
  }
  40%, 100% {
    background-position: 140px;
  }
}
@keyframes shine-circle {
  0% {
    background-position: -32px;
  }
  40%, 100% {
    background-position: 208px;
  }
}
@keyframes shine-lines-sidebar {
  0% {
    background-position: -100px;
  }
  40%, 100% {
    background-position: 140px;
  }
}
@keyframes shine-circle-sidebar {
  0% {
    background-position: -32px;
  }
  40%, 100% {
    background-position: 208px;
  }
}