.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.toggle-container {
  display: flex;
  align-items: center;
}

.toggle {
  display: flex;
  margin-right: 5px;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  min-width: 49px;
  height: 24px;
  background: #7b7b7b;
  border-radius: 12px;
  position: relative;
  transition: background-color 0.2s;
}
.toggle--checked {
  background: #29b662;
}
.toggle--checked-disabled {
  background: #a0d9b9;
  cursor: not-allowed;
}
.toggle__checkbox {
  height: 0;
  width: 0;
  visibility: hidden;
}
.toggle__checkbox:checked + span {
  left: calc(100% - 3px);
  transform: translateX(-100%);
}
.toggle__button {
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  width: 18px;
  height: 18px;
  border-radius: 18px;
  transition: 0.2s;
  background: #fff;
  box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
}
.toggle__label {
  font-size: 16px;
  font-weight: 400;
  pointer-events: none;
}
.toggle::before {
  visibility: hidden;
}
.toggle--disabled {
  background: #d8d8d8;
  cursor: not-allowed;
}