  font-family: "react-editor-icons";
  src: url("/src/fonts/css/font/editor-icons.eot?30795734");
  src: url("/src/fonts/css/font/editor-icons.eot?30795734#iefix") format("embedded-opentype"), url("/src/fonts/css/font/editor-icons.woff2?30795734") format("woff2"), url("/src/fonts/css/font/editor-icons.woff?30795734") format("woff"), url("/src/fonts/css/font/editor-icons.ttf?30795734") format("truetype"), url("/src/fonts/css/font/editor-icons.svg?30795734#editor-icons") format("svg");
