.list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.list__item a {
  text-decoration: none;
}
.list__item a:hover {
  color: #1f1f1f;
}
.list__item svg {
  margin-right: 10px;
}
.list--inline > li {
  display: inline-block;
}

.list-item--table {
  padding: 16px 0;
  border-bottom: 1px solid #e6e4e4;
}
.list-item--table:last-child {
  border: 0;
}
.list-item--table > div {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.list-item--table > div div:nth-child(1) {
  flex: 1;
}
.list-item--table > div div {
  padding: 0 30px;
}

.list-item--list {
  margin: 0 30px;
  padding: 4px 0;
}
.list-item--list > div {
  text-align: left;
  font-size: 14px;
}

.list-item--block {
  margin: 0 20px;
  padding: 2px 0;
}
.list-item--block > div {
  text-align: left;
  font-size: 14px;
}

.list-item--dropdown {
  padding: 0;
  font-size: 14px;
  min-height: 34px;
  line-height: 34px;
}
.list-item--dropdown span {
  padding: 10px 0;
}
.list-item--dropdown a {
  display: flex;
  align-items: center;
  padding: 0 15px;
  font-weight: 400;
}
.list-item--dropdown:hover {
  background: #e6e4e4;
  color: #1f1f1f;
  cursor: pointer;
}
.list-item--dropdown:hover svg {
  color: #1f1f1f;
}

.list-item--menu {
  color: #5d92ff;
  position: relative;
}
.list-item--menu > div {
  position: relative;
  z-index: 2;
  width: 100%;
}
.list-item--menu span {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.list-item--menu svg {
  color: #5d92ff;
  margin-right: 10px;
}
.list-item--menu a {
  color: #5d92ff;
  font-size: 14px;
  padding: 10px 8px 10px 28px;
  display: flex;
  align-items: center;
  line-height: normal;
}
.list-item--menu a:active, .list-item--menu a:focus {
  color: #5d92ff;
}
.list-item--menu:hover a {
  color: #5d92ff;
}
.list-item--menu:hover::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #1b46a2;
  border-radius: 0;
}
.list-item--menu:hover svg {
  color: #5d92ff;
}

.list-item--menu-active a {
  color: white;
  position: relative;
  font-weight: 600;
}
.list-item--menu-active a::after {
  content: "";
  position: absolute;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  top: 50%;
  left: 0px;
  transform: translateY(-50%);
}
.list-item--menu-active a:active, .list-item--menu-active a:focus {
  color: white;
}
.list-item--menu-active svg {
  color: white;
}
.list-item--menu-active:hover a {
  color: white;
}
.list-item--menu-active:hover a::after {
  border-left-color: white;
  border-top-color: transparent;
  border-bottom-color: transparent;
}
.list-item--menu-active:hover svg {
  color: white;
}

.list-item--small a {
  font-size: 12px;
  padding: 10px 8px 10px 28px;
}

.list-item--margin {
  margin-left: 20px;
}

.list--ordered-decimal {
  list-style: decimal;
}
.list--ordered-disc {
  list-style: disc;
}
.list--ordered-check {
  list-style: none;
}
.list--ordered-check li {
  margin: 0;
  padding-left: 30px;
  position: relative;
}
.list--ordered-check li:before {
  font-family: "react-editor-icons";
  content: "\e873";
  font-size: 14px;
  font-weight: 600;
  color: #2550aa;
  position: absolute;
  left: 0;
}

:global .sidebar--dark .List__list-item--menu-active a::after,
:global .sidebar--default .List__list-item--menu-active a::after {
  border-left: 5px solid white;
}
:global .sidebar--white .List__list-item--menu-active a::after {
  border-left: 5px solid #2550aa;
}