.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.spreader {
  display: inline-block;
}

.spreader--mini {
  padding: 0 5px 0 0;
}

.spreader--tiny {
  padding: 0 5px;
}

.spreader--small {
  padding: 0 10px;
}

.spreader--medium {
  padding: 0 15px;
}

.spreader--large {
  padding: 0 25px;
}

.spreader--x-large {
  padding: 0 30px;
}

.spreader--big {
  padding: 0 40px;
}

.spreader--huge {
  padding: 0 50px;
}