.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.accordion__item {
  width: auto;
}
.accordion__item--title {
  cursor: pointer;
  padding-left: 30px;
  padding-right: 50px;
  height: 68px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.accordion__item--title-small {
  height: 35px;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 14px;
}
.accordion__item--title > div {
  display: flex;
  align-items: center;
}
.accordion__item--content {
  padding: 0 30px;
  height: 0;
  overflow: hidden;
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
.accordion__item--content-small {
  padding: 0 15px;
}
.accordion__item--content-open {
  padding-top: 15px;
  padding-bottom: 15px;
  height: max-content;
  opacity: 1;
  transition: all 0.4s ease-out;
}