.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.checkbox-container {
  display: flex;
}
.checkbox-container .checkbox__input {
  z-index: 0;
  position: relative;
  display: inline-block;
  margin-bottom: 0;
}
.checkbox-container .checkbox__input input {
  appearance: none;
  z-index: -1;
  position: absolute;
  left: -10px;
  top: -8px;
  display: block;
  margin: 0;
  width: 40px;
  height: 40px;
  box-shadow: none;
  outline: none;
  opacity: 0;
  transform: scale(1);
  pointer-events: none;
  transition: opacity 0.3s, transform 0.2s;
}
.checkbox-container .checkbox__input input:checked, .checkbox-container .checkbox__input input:indeterminate {
  background-color: #2550aa;
}
.checkbox-container .checkbox__input input:checked + div::before, .checkbox-container .checkbox__input input:indeterminate + div::before {
  border-color: #2550aa;
  background-color: #2550aa;
}
.checkbox-container .checkbox__input input:checked + div::after, .checkbox-container .checkbox__input input:indeterminate + div::after {
  border-color: white;
}
.checkbox-container .checkbox__input input:indeterminate {
  background-color: #2550aa;
}
.checkbox-container .checkbox__input input:indeterminate + div::after {
  border-left: none;
  transform: translate(3px, 4px);
}
.checkbox-container .checkbox__input input:focus {
  opacity: 0;
  outline: none;
}
.checkbox-container .checkbox__input input:active {
  opacity: 1;
  transform: scale(0);
  transition: transform 0s, opacity 0s;
}
.checkbox-container .checkbox__input input:active + div::before {
  border-color: #2550aa;
}
.checkbox-container .checkbox__input input:checked:active {
  opacity: 1;
  transform: scale(0);
  transition: transform 0s, opacity 0s;
}
.checkbox-container .checkbox__input input:checked:active + div::before {
  border-color: transparent;
  background-color: #2550aa;
  opacity: 0.6;
}
.checkbox-container .checkbox__input input:disabled {
  opacity: 0;
}
.checkbox-container .checkbox__input input:disabled + div {
  color: #2550aa;
  opacity: 0.38;
  cursor: initial;
}
.checkbox-container .checkbox__input input:disabled + div::before {
  border-color: currentColor;
}
.checkbox-container .checkbox__input input:checked:disabled + div::before, .checkbox-container .checkbox__input input:indeterminate:disabled + div::before {
  border-color: transparent;
  background-color: currentColor;
}
.checkbox-container .checkbox__input div::before {
  content: "";
  display: inline-block;
  box-sizing: border-box;
  margin: 3px 11px 3px 1px;
  border: solid 2px;
  border-color: #a7a7a7;
  border-radius: 2px;
  width: 18px;
  height: 18px;
  vertical-align: top;
  transition: border-color 0.2s, background-color 0.2s;
}
.checkbox-container .checkbox__input div::after {
  content: "";
  display: block;
  position: absolute;
  top: 4px;
  left: 2px;
  width: 8px;
  height: 3px;
  border: solid 2px transparent;
  border-right: none;
  border-top: none;
  transform: translate(3px, 4px) rotate(-45deg);
}
.checkbox-container .checkbox__input:hover input {
  opacity: 0.04;
}
.checkbox-container .checkbox__input:hover input:focus {
  opacity: 0.16;
}
.checkbox-container .checkbox__input__label {
  font-size: 16px;
  font-weight: 400;
  pointer-events: none;
  margin-bottom: 0;
}