.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.info-bar {
  position: relative;
  line-height: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
}
.info-bar .info__bar {
  padding: 0 25px;
  text-align: left;
  flex: 2;
  margin: 0;
  font-size: 14px;
  width: 210px;
}
.info-bar--box {
  width: 60px;
  height: 60px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}
.info-bar--box:after {
  content: "";
  position: absolute;
  right: 100%;
  top: 23px;
  width: 0;
  height: 0;
  left: 60px;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
}
.info-bar--alert {
  background: #ffebed;
}
.info-bar--alert-box {
  background: #ff3245;
}
.info-bar--alert-box:after {
  border-left: 10px solid #ff3245;
}
.info-bar--alert i {
  align-items: center;
  color: white;
}
.info-bar--warning {
  background: #fff7e5;
}
.info-bar--warning-box {
  background: #ffb000;
}
.info-bar--warning-box:after {
  border-left: 10px solid #ffb000;
}
.info-bar--warning i {
  align-items: center;
  color: white;
}
.info-bar--info {
  background: #e5edff;
}
.info-bar--info-box {
  background: #5d92ff;
}
.info-bar--info-box:after {
  border-left: 10px solid #5d92ff;
}
.info-bar--info i {
  align-items: center;
  color: white;
}