.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.badge {
  position: relative;
  padding: 0 5px;
  text-transform: uppercase;
  font-size: 12px;
  color: white;
  border-radius: 2px;
  line-height: 20px;
  display: inline-flex;
  align-items: baseline;
  height: 20px;
  margin-right: 5px;
}
.badge a {
  font-weight: 400;
  color: white;
  text-decoration: none;
}
.badge__tooltip {
  max-width: 105px;
  display: inline-block;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.badge--accent-1 {
  background: #9368b3;
}
.badge--accent-2 {
  background: #00bd9e;
}
.badge--accent-3 {
  background: #f99e31;
}
.badge--accent-4 {
  background: #24bd9c;
}
.badge--accent-5 {
  background: #610fb7;
}
.badge--accent-6 {
  background: #9363b6;
}
.badge--accent-7 {
  background: #f27589;
}
.badge--accent-8 {
  background: #7b7b7b;
}
.badge--alert {
  background: #ff3245;
}
.badge--warning {
  background: #ffb000;
}
.badge--success {
  background: #29b662;
}
.badge--info {
  background: #5d92ff;
}
.badge--pending {
  background: #7988a0;
}
.badge--indicator {
  border-radius: 50px;
}
.badge--paid {
  background: #29b662;
}
.badge--open {
  background: #ffb000;
}
.badge--canceled {
  background: #ff3245;
}
.badge--ai {
  background: #29cbb7;
}
.badge svg {
  vertical-align: -1px;
  margin-right: 5px;
}