.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.wrapper {
  border: 1px solid #edecec;
  width: fit-content;
}

.button {
  background: none;
  color: #7b7b7b;
  border: none;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
}
.button:hover {
  background-color: #edecec;
}
.button--active {
  text-decoration: none;
  background-color: #edecec;
  color: #000000;
}