.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 4, 35, 0.6);
  z-index: 250996;
  left: 0;
  top: 0;
}
.backdrop__index-4 {
  z-index: 4;
}
.backdrop__index-6 {
  z-index: 6;
}
.backdrop__index-8 {
  z-index: 8;
}

@supports (backdrop-filter: none) {
  .backdrop {
    backdrop-filter: blur(5px);
  }
}