.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.color-line:before {
  content: "";
  width: 15px;
  height: 10px;
  position: absolute;
  left: -25px;
  top: 6px;
  border-radius: 250px;
}
.color-line--vertical:before {
  width: 9px;
  height: 100%;
  top: 0px;
}
.color-line--success:before {
  background: #29b662;
}
.color-line--warning:before {
  background: #ffb000;
}
.color-line--alert:before {
  background: #ff3245;
}
.color-line--info:before {
  background: #5d92ff;
}