.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.react-tooltip {
  border-radius: 0 !important;
  opacity: 1 !important;
  max-width: 250px;
  word-break: break-word;
  white-space: normal;
  overflow: visible !important;
  font-weight: 400;
  line-height: normal;
  font-size: 12px;
  font-family: "Inter";
}
.react-tooltip-left {
  text-align: left;
}
.react-tooltip-right {
  text-align: right;
}
.react-tooltip-center {
  text-align: center;
}