.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.panel {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  background-color: white;
  margin-bottom: 3px;
}
.panel--background {
  background-color: #2550aa;
}
.panel--background > span {
  color: white;
}
.panel--padding-default {
  padding: 30px;
}
.panel--padding-tiny {
  padding: 10px 0;
}
.panel--padding-nolr {
  padding: 30px 0 10px;
}
.panel--padding-none {
  padding: 0;
}
.panel--padding-bottom-tiny {
  padding: 0 0 10px;
}
.panel--padding-input {
  padding: 30px 30px 10px 30px;
}
.panel--adjust-height {
  height: 100%;
}
.panel--shadow-none {
  box-shadow: none;
}