.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.avatar {
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.avatar p {
  display: block;
  visibility: hidden;
  position: relative;
  width: 11px;
}
.avatar p:first-letter {
  visibility: visible;
}
.avatar img {
  border-radius: 50%;
}
.avatar--blank {
  background: #9368b3;
}
.avatar--tiny {
  width: 36px;
  height: 36px;
}
.avatar--tiny img {
  width: inherit;
}
.avatar--medium {
  width: 42px;
  height: 42px;
}
.avatar--medium img {
  width: inherit;
}