.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.block-section {
  margin: 0 auto;
  width: 1054px;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.block-section__panel {
  width: 1054px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.block-section__panel--reverse {
  flex-direction: row-reverse;
}
.block-section__panel--content {
  padding: 10px 70px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}