.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.divider {
  height: 1px;
}
.divider--dropdown {
  background: #dfdddd;
  margin: 0;
}
.divider--normal {
  background: #dfdddd;
  margin: 0;
}
.divider--menu {
  background: #5d92ff;
  margin: 10px 0;
  width: 30px;
  margin-left: 20px;
}
.divider--horizontal {
  background: #dfdddd;
  margin: 0 15px;
}
.divider--vertical {
  transform: rotate(180deg);
}