.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.bar {
  height: 12px;
  position: absolute;
  border-radius: 250px;
}
.bar__background {
  width: 100%;
  opacity: 0.2;
}
.bar__fulfillment {
  animation: fill 2s ease-out;
}
.bar__fulfillment--bordered {
  top: 3px;
  left: 3px;
  height: 10px;
}
.bar__fulfillment--no-animation {
  animation: none;
}
.bar--success {
  background-color: #29b662;
}
.bar--warning {
  background-color: #ffb000;
}
.bar--alert {
  background-color: #ff3245;
}
.bar--progress {
  background-color: #5d92ff;
}
.bar--brand {
  background-color: #2550aa;
}
.bar__border--white {
  border: 3px solid white;
  background-color: white;
  opacity: 1;
  height: 10px;
}
.bar__border--grey {
  border: 3px solid #f2f2f2;
  background-color: #f2f2f2;
  opacity: 1;
  height: 10px;
}

.container {
  position: relative;
  margin-bottom: 15px;
}
.container--bordered {
  margin-right: 6px;
}

.max-result {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  color: #cccccc;
  font-size: 14px;
}

.limit-exceeded__info {
  font-size: 12px;
  display: flex;
  justify-content: flex-end;
  color: #4c4c4c;
}

@keyframes fill {
  0% {
    width: 0px;
  }
}