.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.custom-link {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-decoration: none;
}
.custom-link:hover {
  text-decoration: underline;
}
.custom-link--active {
  color: #2550aa;
}
.custom-link--active:hover {
  color: #4168c4;
}
.custom-link--inactive {
  color: #d8d8d8;
  pointer-events: none;
  cursor: default;
}
.custom-link--dark {
  color: #1f1f1f;
}
.custom-link--dark:hover {
  color: #2550aa;
}
.custom-link--underlined {
  text-decoration: underline;
}
.custom-link--10 {
  font-size: 10px;
}
.custom-link--12 {
  font-size: 12px;
}
.custom-link--14 {
  font-size: 14px;
}
.custom-link--16 {
  font-size: 16px;
}