.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.boxBackground {
  display: flex;
  text-align: center;
  color: white;
  align-items: center;
  align-self: center;
  justify-content: center;
  align-content: center;
  font-size: 16px;
  padding: 2px 5px;
  font-weight: 600;
}
.boxBackground--success {
  background-color: #29b662;
}
.boxBackground--warning {
  background-color: #ffb000;
}
.boxBackground--alert {
  background-color: #ff3245;
}
.boxBackground--progress, .boxBackground--info {
  background-color: #009fe3;
}