.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.loader {
  margin: 0 auto;
  width: 115px;
  height: 75px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader__spinner {
  display: block;
}
.loader__spinner--inner {
  width: 38px;
  height: 38px;
  animation: spin 2.5s linear infinite;
  border-radius: 300px;
  padding: 4px;
}
.loader__spinner--inner svg {
  stroke-dasharray: 86;
  stroke-dashoffset: 0;
  animation: dash 1s ease-in-out infinite;
  animation-direction: alternate-reverse;
  stroke-width: 4px;
  transform: rotate(180deg);
}
.loader__triangle {
  fill: #f27589;
  position: absolute;
  top: 46px;
  left: 79px;
  transform: rotate(154deg);
  animation: move-triangle 3.2s infinite;
}
.loader__square {
  fill: #f8d260;
  position: absolute;
  top: 20px;
  left: 25px;
  transform: rotate(105deg);
  animation: move-square 3.2s infinite;
}
.loader__circle {
  fill: #7271d4;
  position: absolute;
  top: 48px;
  left: 23px;
  opacity: 1;
  animation: move-circle 3.2s infinite;
}
.loader__circle2 {
  fill: #2550aa;
  position: absolute;
  top: 22px;
  left: 83px;
  opacity: 1;
  animation: move-circle-2 3.2s infinite;
}

@keyframes move-triangle {
  0%, 25% {
    top: 46px;
    left: 79px;
  }
  50% {
    top: 60px;
    left: 100px;
  }
  100% {
    top: 46px;
    left: 79px;
  }
}
@keyframes move-square {
  0%, 25% {
    top: 20px;
    left: 25px;
  }
  50% {
    top: 10px;
    left: 10px;
  }
  100% {
    top: 20px;
    left: 25px;
  }
}
@keyframes move-circle {
  0%, 25% {
    top: 48px;
    left: 23px;
  }
  50% {
    top: 60px;
    left: 8px;
  }
  100% {
    top: 48px;
    left: 23px;
  }
}
@keyframes move-circle-2 {
  0%, 25% {
    top: 22px;
    left: 83px;
  }
  50% {
    top: 8px;
    left: 100px;
  }
  100% {
    top: 22px;
    left: 83px;
  }
}
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
@keyframes dash {
  to {
    stroke-dashoffset: 80;
  }
}
.container {
  display: flex;
  justify-content: center;
  height: 128px;
  align-items: center;
}

.main {
  display: flex;
  margin-top: 12px;
}

.one {
  animation-name: load-one;
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
  height: 0px;
  width: 0px;
  border-right: 8px solid transparent;
  border-bottom: 12px solid #2550aa;
  border-left: 8px solid transparent;
  margin-right: 12px;
}

@keyframes load-one {
  30% {
    transform: translateY(-100%);
  }
}
.two {
  animation-name: load-two;
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
  height: 8px;
  width: 16px;
  border-radius: 150px 150px 0 0;
  background-color: #1ec7b8;
  transform: rotate(45deg);
  margin-right: 12px;
}

@keyframes load-two {
  50% {
    transform: translateY(-100%) rotate(45deg);
  }
}
.three {
  animation-name: load-three;
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
  height: 12px;
  width: 12px;
  background-color: #f8d260;
  margin-right: 12px;
}

@keyframes load-three {
  70% {
    transform: translateY(-100%);
  }
}
.forth {
  animation-name: load-forth;
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
  height: 12px;
  width: 12px;
  background-color: #f27589;
  border-radius: 12px;
}

@keyframes load-forth {
  90% {
    transform: translateY(-100%);
  }
}