.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.textarea {
  resize: none;
  margin: 10px 0 0;
  box-sizing: border-box;
  width: 100%;
  height: 414px;
  background-color: #1f1f1f;
  border: 1px solid #dddddd;
  font-family: "Courier New", Courier, monospace;
  color: white;
  font-size: 13px;
  padding: 11px 10px;
}
.textarea:focus {
  box-shadow: none;
  border: 1px solid #1f1f1f;
}