.text--truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "react-editor-icons";
  src: url("../fonts/css/font/editor-icons.eot");
  src: url("../fonts/css/font/editor-icons.eot?#iefix") format("embedded-opentype"), url("../fonts/css/font/editor-icons.woff2") format("woff2"), url("../fonts/css/font/editor-icons.woff") format("woff"), url("../fonts/css/font/editor-icons.ttf") format("truetype"), url("../fonts/css/font/editor-icons.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 22px 0;
}

.step {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 0 15px;
}
.step__description--completed {
  font-size: 14px;
  position: relative;
  color: #7b7b7b;
}
.step__description--current {
  font-size: 14px;
  position: relative;
  color: #1f1f1f;
}
.step__description--next {
  font-size: 14px;
  position: relative;
  color: #cccccc;
}
.step:after {
  content: "";
  background: #dddddd;
  height: 1px;
  width: 15px;
  margin-left: 15px;
  top: 45%;
}
.step:last-of-type:after {
  display: none;
}